import Common from "../../lib/common.js"
export default {
    data(){
        return {
          userInfo:{headPic: require('@/assets/static/images/bh1.png'),userName:'默认'},
          text:[
            {
                text: '我的业务',
                url: 'Process',
                tip: ''
            },
            {
              text: '交易状态查询',
              url: 'Deal',
              tip: ''
            },{
              text: '操作演示',
              url: 'Teach',
              tip: ''
            },
            {
              text: '联系我们',
              url: 'Help',
              tip: ''
            }
          ]
        }
    },
    methods:{
      clickRoute(){
        this.$emit('handerSelect',4);  //子组建触发父组建方法
      }
    },
    mounted() {
      var that = this;
      Common.FindUser(function (res) {
        that.userInfo.userName = res.data;
      },function () {

      })
    }
}



