import Common from '@/lib/common'
export default {
  data(){
    return{
      dealStatus:"未扣款",
      taxStatus:"未纳税",
      payWrongText:"",
      taxWrongText:"",
      paySuccessColor:"red",
      taxSuccessColor:"red"
    }
  },
  mounted(){
    var that =this;
    Common.getPayStatus(function(res){
      if(res.data.code!="200"){
        that.dealStatus="未缴费";
        that.taxStatus="未缴税";
        that.payWrongText="无此记录！";
        that.taxWrongText="无此记录！";
      }else{
        if(res.data.context[0]==''){
          that.dealStatus="未缴费";
          that.taxStatus="未缴税";
          that.payWrongText="无此记录！";
          that.taxWrongText="无此记录！";
        }else{
          if(res.data.context[0].xtZxbz=="0" && res.data.context[0].status=="0"){
            that.dealStatus="缴费成功！";
            that.paySuccessColor="green"
            that.payWrongText="";
          }
        }
        if(res.data.context[1]==''){
          that.taxStatus="未缴税";
          that.taxWrongText="无此记录！";
        }else{
          if(res.data.context[1].procResult=="000"){
            that.taxStatus="缴税成功";
            that.taxWrongText="";
            that.taxSuccessColor="green";
          }else{
            that.taxStatus="缴税失败";
            that.taxWrongText=''+res.data.context[1].procResult + ',' +res.data.context[1].errTex;
            that.taxSuccessColor="red";
          }
        }
      }
    });
  },
  methods: {
    goback1() {
      this.$router.push("/index")
    },
    loadAlert(){
      Common.loading(this,'敬请期待！');
    }
  }

}



