import SwipperImage from "@/components/CommComponents/SwipperImage"
import Common from "@/lib/common"
export default {
     components:{
        SwipperImage
    },
    data(){
        return{
            bright: ['', '', '', ''],
            swiperimgs: [
                {
                    url: 'javascript',
                  img: require('@/assets/static/images/1.jpg')
                },
                {
                    url: 'javascript',
                    img: require('@/assets/static/images/1.jpg')
                },
                {
                    url: 'javascript',
                    img: require('@/assets/static/images/1.jpg')
                }
            ],
            text: [
                {
                    text: '自助办理',
                    url: 'personal',
                    tip: '',
                    sclass: 'go',
                    img: 'img/personal.png'
                },
            ],
            goitem: [
                {
                    border: '0rpx 0rpx 0rpx 1rpx',
                    first: '房产数据',
                    secend: '一次授权自动同步',
                    src: require('@/components/BusinessHandManage/img/synchronization.png'),
                    url: 'none',
                    hidden: false
                },
                {
                    border: '0rpx 1rpx 0rpx 0rpx',
                    first: '资料审核',
                    secend: '在家体验便民服务',
                    src: require('@/components/BusinessHandManage/img/BusinessManagement.png'),
                    url: 'personal',
                    hidden:false
                }

            ],
            clientKey: "",
            backhidden:false,
            kydjtimeout:'',
            gotoUrl:''
        }
    },
    methods:{
         bright1(key){
            var i = key
            this.bright[i] = 'bright';
        },
        normal:function(key){
            var i = key
            this.bright[i] = '';
        },
        personal(val){
           if(val=="personal"){

             var that = this
             Common.FindUser(function (){
               Common.Spjl(function (data) {
                 that.$router.push({
                   path:"process",
                   query: {data:data}
                 });
               });

             },function () {
               that.$router.push({
                 path:"login",
                 params: {}
               });
             });
           }else{
             //跳转到房管数据同步接口
             var that1 = this
             Common.FindUser(function (){
               Common.Spjl(function (data) {

                 if(data.xtSqbz ==="0"){
                   that1.$router.push({
                     path:"Agree",
                     query: {data:data}
                   });
                 }else{
                   that1.$router.push({
                     path:"SyncFgyInfo",
                     query: {data:data}
                   });
                 }
               });

             // var that = this;
             // Common.FindUser(function () {
             //   that.$router.push({
             //     path: "SyncFgyInfo",
             //     query: {}
             //   });
             }, function () {
               that.$router.push({
                 path: "login",
                 params: {}
               });
             });
           }

        },

    },
    mounted(){

    }
}



