  import Common from '@/lib/common'

export default {
  props:{
    spzt:{
      type:String,
      default:null
    },
    cards:{
      type:Array,
      default:null
    }
  },
    data(){
        return{
            backhidden:true,
            spwz:"您的资料已经提交税务局审核",
            spwzsecend:"3-5个工作日内审核资料，请耐心等待",//灰色提示字
            spljwz:"下一步"//审批连接的文字变化
        }
    },
  mounted(){//根据状态修改文字
    switch (this.spzt) {
      case 1:
      case "1":
        this.spwz="您的资料已经提交税务局审核"
        this.spwzsecend="3-5个工作日内审核资料，请耐心等待"
        break;
      case 2:
      case "2":
        this.spwz="您的资料已经通过税务局审核"
        this.spwzsecend="税单正在打印申请中，请您耐心等候"
        break;
      case 3:
      case "3":
        this.$emit('goOtherPage');
        break;
      case 4:
      case "4":
        this.spwz="您的缴费申请已提交"
        this.spwzsecend="银行正在处理您的缴费申请，请您耐心等候"
        break;
      case 6:
      case "6":
        this.spwz="缴费成功"
        this.spwzsecend="税务局正在处理您的资料，请耐心等候..."
        this.spljwz="返回首页"
        break;
      case 7:
      case "7":
        this.spwz="邮件已寄出"
        this.spwzsecend="您的邮件已寄出"
        break;
      case "8":
        this.spwz="已签收"
        this.spwzsecend="已完成在线扣税"
        break;

      case 9:
      case "9":
        this.spwz="缴费成功"
        this.spwzsecend="正在处理您的申请，我们将在3-5个工作日提醒您前来自取"
        break;
    }
  },
    methods:{
        returnindex(){
            var that = this;
            //这里根据spzt（审批状态）做对应的文字修改与其它操作就行
            if(this.spzt=="7"){
            // wx.showModal({
            //     title: '是否确认签收？',
            //     content: '点击确定签收，取消返回首页',
            //     confirmText:'确认签收',
            //     cancelText:'返回首页',
            //     success:function(res){
            //     if(res.confirm){
            //         //签收完成，调用签收方法，修改状态并返回首页
            //         wx.showToast({
            //         title: '签收成功！',
            //         icon:"none",
            //         duration:2000,
            //         })
            //         var url = commonJs.url;
            //         wx.request({
            //         url: url + "/qishui/qsapp/api/apply/yqs",
            //         method: "GET",
            //         header: commonJs.getHeader(that.data.clientKey),
            //         data: {
            //             applyId: that.data.applyId,
            //         },
            //         success: function () {
            //             wx.navigateBack({
            //             delta: 1,
            //             })
            //         }
            //         })
            //
            //     }
            //     else{
            //         wx.navigateBack({
            //         delta: 1,
            //         })
            //     }
            //     }
            // })
              this.$confirm('是否确认签收?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
              }).then(() => {
                Common.postGet(that);
              }).catch(() => {
                this.$message({
                  type: 'info',
                  message: '用户已取消'
                });
              });
            } else if (this.spzt == "1" || this.spzt == "2" || this.spzt == "8" || this.spzt == "9"){
              this.$router.push("index")
            }else if(this.spzt=="5"){
              this.spljwz = "配置邮寄"
              this.$router.push("/YoujiPages/HandlePost")
            }
            // else if(this.spzt=="6"){
            //   this.spljwz = "配置邮寄"
            //   this.$router.push("/YoujiPages/HandlePost")
            // }
            else{
              this.$router.push("index")
            }
            
        },
    }
}



